import { Text } from '../../../atoms'
import {
  Horizontal,
  Column,
  Row
} from '../../../layout'
import styles from '../ShelfAlvi.module.css'
import { ChipOffer } from '../ChipOffer'
import { currencyFormat } from '../../../../helpers'

export interface PartnerPriceProps {
  _id?: string;
  isMobile?: boolean;
  minQuantity?: number;
  orientation?: 'horizontal' | 'vertical';
  percentualDiscount?: number;
  promotionalPrice?: number;
  unit?: string;
  pdp?: boolean;
}

export const PartnerPrice = ({
  _id,
  isMobile = false,
  minQuantity,
  orientation = 'horizontal',
  pdp = false,
  percentualDiscount,
  promotionalPrice,
  unit
}: PartnerPriceProps): React.ReactElement<PartnerPriceProps> => {

  const measurementUnit = unit || 'un'
  const sizeTextFrom = pdp ? 'lg' : 'sm'
  const sizeTextCu = pdp ? 'lg' : 'xs'
  const sizeTextPrice = pdp ? '3xl' : 'sm'
  const sizeTextChip = pdp ? 'md' : 'xs'
  const paddingChip = pdp ? '1px 0 1px 4px' : '5px 0 5px 0'
  const customWidth = pdp ? '100' : '110'
  const widthShip = pdp ? '84px' : '70px'
  const customClass = pdp ? styles.textPricePromotionCcuPdp : styles.textPricePromotionCcu

  return (
    <>
      <Row alignItems='center' key={_id}>
        <Column customWidth={customWidth}>
          <Text color='gray' fontSize={sizeTextFrom}>
            Desde {minQuantity} {measurementUnit}
          </Text>
        </Column>
        <Column>
          <Row justifyContent='end'>
            <Text
              color='primary'
              customClassName={styles.textPricePromotion}
              fontSize={sizeTextPrice}
              fontWeight='semibold'
              truncate={30}
              type='span'
            >
              {currencyFormat({ number: promotionalPrice, bool: false })}
            </Text>
            <Text
              color='blue'
              customClassName={customClass}
              fontSize={sizeTextCu}
              fontWeight='regular'
              truncate={30}
              type='span'
            >
              c/u
            </Text>
          </Row>
        </Column>
        {(!isMobile || orientation === 'horizontal') && (
          <Column customWidth='108'>
            <Row justifyContent='start'>
              <ChipOffer
                fontSizeChipOffer={sizeTextChip}
                offer={`Ahorra ${percentualDiscount}%`}
                padding={paddingChip}
                styled={styles.offerDesktop}
                width={widthShip}
              />
            </Row>
          </Column>
        )}
      </Row>
      <Horizontal size={2} />
    </>
  )

}