import React, { useMemo, useState } from 'react'
import { PartnerPrice, PartnerPriceProps } from './PartnerPrice'
import {
  ButtonStatus,
  Icon,
  Link,
  Picture,
  Text,
  TooltipProps,
  Divider
} from '../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../layout'
import {
  currencyFormat,
  getGlobalStyle,
  SmallScreen
} from '../../../helpers'
import {
  AddToCartAndQuantity,
  ChipProps,
  QuantityButtonProps,
  QuantityButtonStylesProps
} from '../../molecules'
import { FloatButtons } from './FloatButtons'
import { PropertiesProducts } from './helpers/PropertiesProducts'
import styles from './ShelfAlvi.module.css'

export interface ShelfAlviProps extends Omit<React.HTMLProps<HTMLInputElement>, 'ref'> {
  [key: string]: unknown;
  brand?: string;
  buttonLabel?: string;
  buttonStatus?: ButtonStatus;
  couponState?: boolean;
  customClassName?: string;
  customWidthQuantityButton?: string;
  format?: string;
  img?: string;
  importantWidth?: string;
  isDisabled?: boolean;
  isLoading?: boolean;
  isLoggedIn?: boolean;
  isMobile?: boolean;
  isPromotion?: boolean;
  itemId?: string | number;
  likeListButtons?: boolean;
  listPrice?: number;
  measurementUnit?: string;
  measurementUnitUn?: string;
  offer?: number | string;
  orientation?: 'horizontal' | 'vertical';
  ppum?: string;
  ppumListPrice?: string;
  price?: number;
  priceSteps?: Array<PartnerPriceProps>;
  promotion?: { image?: string } & ChipProps;
  quantity?: number;
  quantityButtonProps?: QuantityButtonProps;
  quantityButtonStyles?: QuantityButtonStylesProps;
  stock?: boolean;
  title?: string;
  tooltipProps?: TooltipProps;
  typePromotion?: number;
  url?: string;
  validationStatus?: string;
  catchShelfRef?: (props?: object) => void;
  handleOnClick?: (props?: { itemId: string }) => void;
  onClickProduct?: () => void;
  onRedirect?: () => void;
}

const customProperties = (
  importantWidth: string,
  isMobile: boolean,
  orientation: string,
  stock: boolean
) => {
  const shelfVertical = orientation == 'vertical'
  const shelfVerticalMobile = shelfVertical && isMobile
  const shelfHorizontal = orientation == 'horizontal'
  const customMaxWidth = shelfVertical ? '272px' : '100%'
  const customMinHeight = shelfVertical ? '380px' : '218px'
  const customTruncate: typeof Text.arguments.truncate = shelfVerticalMobile ? 20 : 28
  const customJustifyContent: typeof Row.arguments.justifyContent = shelfVerticalMobile ? 'end' : 'start'
  const customPictureClassName = !stock ? styles.outOfStock : ''
  const customWidth = importantWidth || '100'

  return {
    customJustifyContent,
    customMaxWidth,
    customMinHeight,
    customPictureClassName,
    customTruncate,
    customWidth,
    shelfHorizontal,
    shelfVertical
  }
}

const setButtonStatus = (
  buttonStatus: ButtonStatus,
  stock: boolean,
  couponState: boolean
): ButtonStatus => {
  if (!stock || couponState) return 'disabled'
  return buttonStatus
}

const validateMobile = (isMobile: boolean, shelfVertical: boolean, shelfHorizontal: boolean) => {

  const customVerticalMargin = !isMobile ? 6 : 2
  const customFontSize: typeof Text.arguments.fontSize = isMobile ? 'xs' : 'md'
  const regularPriceSpacer: typeof Spacer.arguments.size = shelfVertical && isMobile ? 1 : 80
  const customPadding = shelfHorizontal && isMobile ? '12px 2' : '5px'
  const sizeFormat: typeof Text.arguments.fontSize = isMobile ? 'xs' : 'sm'
  const sizeTitle: typeof Text.arguments.fontSize = isMobile ? 'sm' : 'md'
  const customImageSize = isMobile ? '100px' : '160px'
  const customImagePadding = isMobile ? '20px 0' : '0'
  const sizeInfo: typeof Text.arguments.fontSize = isMobile ? 'sm' : 'md'
  const sizeIconInfo = isMobile ? 16 : 20
  const alignItemsPrice: typeof Row.arguments.alignItems = isMobile ? 'center' : 'baseline'

  return {
    alignItemsPrice,
    customFontSize,
    customImagePadding,
    customImageSize,
    customPadding,
    customVerticalMargin,
    regularPriceSpacer,
    sizeFormat,
    sizeIconInfo,
    sizeInfo,
    sizeTitle
  }
}

const ShelfAlvi = React.forwardRef(function ShelfAlvi({
  brand,
  format,
  img,
  importantWidth,
  couponState = false,
  buttonStatus = 'initial',
  isLoading,
  isLoggedIn,
  isMobile = true,
  itemId,
  likeListButtons = false,
  measurementUnit = 'un',
  measurementUnitUn = 'un',
  orientation = 'horizontal',
  price,
  priceSteps,
  quantity = 0,
  quantityButtonProps,
  quantityButtonStyles,
  stock = true,
  title,
  tooltipProps,
  url,
  validationStatus,
  onRedirect,
  catchShelfRef,
  handleOnClick,
  onClickProduct
}: ShelfAlviProps, ref?: React.RefObject<HTMLDivElement>): React.ReactElement<ShelfAlviProps> {
  const {
    customJustifyContent,
    customMaxWidth,
    customMinHeight,
    customPictureClassName,
    customTruncate,
    customWidth,
    shelfHorizontal,
    shelfVertical
  } = customProperties(importantWidth, isMobile, orientation, stock)

  const customButtonStatus = setButtonStatus(buttonStatus, stock, couponState)

  const {
    alignItemsPrice,
    customFontSize,
    customImagePadding,
    customImageSize,
    customPadding,
    customVerticalMargin,
    regularPriceSpacer,
    sizeFormat,
    sizeIconInfo,
    sizeInfo,
    sizeTitle
  } = validateMobile(isMobile, shelfVertical, shelfHorizontal)

  const {
    isWeighable,
    showInValidation,
    showPriceSteps,
    sortPriceStep,
    textButton
  } = PropertiesProducts({
    validationStatus,
    isLoggedIn,
    product: {
      priceSteps,
      measurementUnit,
      measurementUnitUn
    },
    stock,
    couponState
  })

  const clubAlvi = false
  const buttonJustifyContent = orientation === 'vertical' ? 'center' : 'start'
  const [shelfRef, setShelfRef] = useState()

  const handleImgVal = useMemo(() => {
    shelfRef && catchShelfRef && catchShelfRef(shelfRef)
    return true
  }, [shelfRef])

  return (
    <Column
      customWidth={customWidth}
      justifyContent='center'
      maxWidth={customMaxWidth}
      padding={customPadding}
      ref={setShelfRef}
      val={handleImgVal}
    >
      <Spacer.Horizontal size={12} />
      <Container
        backgroundColor={getGlobalStyle('--color-alvi-base-white')}
        borderRadius='20px'
        customClassName={styles.shelf}
        justifyContent='center'
        maxWidth='100%'
        minHeight={customMinHeight}
        padding={getGlobalStyle('--border-radius-sm')}
        position='relative'
        ref={ref}
      >
        {likeListButtons && <FloatButtons />}
        <Row>
          {orientation === 'horizontal' && (
            <Column
              customWidth='46'
              justifyContent='center'
              onClick={onClickProduct}
            >
              <Picture
                alt={title}
                anchor={true}
                border='none'
                borderRadius='none'
                className={customPictureClassName}
                height='90px'
                objectFit='contain'
                src={img}
                url={url}
                width='100%'
              />
            </Column>
          )}
          <Column padding='0 0 0 6px'>
            {orientation === 'vertical' && (
              <>
                <Row justifyContent='center' padding={customImagePadding}>
                  <Picture
                    alt={title}
                    anchor={true}
                    border='none'
                    borderRadius='none'
                    className={customPictureClassName}
                    height={customImageSize}
                    objectFit='contain'
                    src={img}
                    url={url}
                    width='100%'
                  />
                </Row>
                <Spacer.Horizontal size={16} />
              </>
            )}
            <Row>
              <Column clickable='pointer' onClick={onRedirect}>
                <Text
                  clickable='pointer'
                  color='black'
                  customClassName={styles.brandText}
                  fontSize={sizeFormat}
                  fontWeight='semibold'
                >
                  {brand.toLowerCase()}
                </Text>
                <Spacer.Horizontal size={2} />
                <Link
                  href={url}
                  title={title}
                  underline='none'
                >
                  <Text
                    clickable='pointer'
                    color='black'
                    fontSize={sizeTitle}
                    fontWeight='regular'
                    truncate={customTruncate}
                  >
                    {title.charAt(0) + title.toLowerCase().slice(1)}
                  </Text>
                </Link>
                <Spacer.Horizontal size={2} />
                <Row minHeight='16.3px'>
                  <Text
                    color='gray'
                    fontSize={sizeFormat}
                    truncate={customTruncate}
                  >
                    {format}
                  </Text>
                </Row>
              </Column>
            </Row>

            {stock &&
              <Row>
                <Column justifyContent='start' minHeight='72px'>
                  {!isWeighable &&
                    <>
                      <Spacer.Horizontal size={8} />
                      <Column customWidth='70'>
                        <Row alignItems='center'>
                          <Text
                            color='primary'
                            customClassName={styles.textBlue}
                            fontSize={customFontSize}
                            fontWeight='semibold'
                            truncate={30}
                          >
                            Precio socio
                          </Text>
                          <Icon customSize={20} name='LogoAlvi' />
                        </Row>
                      </Column>
                      <Column minHeight='44px'>
                        {showPriceSteps && sortPriceStep.map((step: JSX.IntrinsicAttributes & PartnerPriceProps, index: number) => {
                          return (
                            <PartnerPrice
                              isMobile={isMobile}
                              key={index}
                              orientation={orientation}
                              unit={measurementUnit}
                              {...step}
                            />
                          )
                        })}

                        <SmallScreen>
                          <>
                            {clubAlvi &&
                              <Row alignItems='center' minHeight='44px'>
                                <Icon
                                  color={getGlobalStyle('--color-alvi-primary-blue')}
                                  customSize={sizeIconInfo}
                                  name='Info'
                                />
                                <Spacer.Vertical size={8} />
                                <Text color='gray' fontSize={sizeInfo}>Únete al Club Alvi</Text>
                              </Row>
                            }

                            {showInValidation && isMobile &&
                              <Row alignItems='center' minHeight='44px'>
                                <Icon
                                  color={getGlobalStyle('--color-alvi-primary-blue')}
                                  customSize={sizeIconInfo}
                                  name='Info'
                                />
                                <Spacer.Vertical size={8} />
                                <Text color='gray' fontSize={sizeInfo}>En validación</Text>
                              </Row>
                            }
                          </>
                        </SmallScreen>

                      </Column>
                    </>
                  }
                </Column>
              </Row>
            }

            {!stock &&
              <>
                <Spacer.Horizontal size={12} />
                <Row minHeight='95px'>
                  <Row
                    backgroundColor={getGlobalStyle('--color-neutral-gray-dark')}
                    borderRadius={getGlobalStyle('--border-radius-2xs')}
                    justifyContent='center'
                    maxHeight='15px'
                    maxWidth='60px'
                  >
                    <Text color='white' fontSize='sm'>
                      sin stock
                    </Text>
                  </Row>
                </Row>
              </>
            }

            <Column>
              {stock &&
                <>
                  {!isWeighable ?
                    <Divider color={getGlobalStyle('--color-alvi-neutral-gray')} verticalMargin={customVerticalMargin} />
                    :
                    <Column minHeight='13px' />
                  }
                  <Spacer.Horizontal size={2} />
                  <Row alignItems={alignItemsPrice} justifyContent='center'>
                    <Column customWidth='116'>
                      <Text
                        color='black'
                        fontSize={sizeFormat}
                        fontWeight='semibold'
                      >
                        Precio regular
                      </Text>
                    </Column>
                    <Column customWidth='60'>
                      <Row alignItems={alignItemsPrice} justifyContent={customJustifyContent}>
                        <Text
                          color='black'
                          fontSize='sm'
                          fontWeight='semibold'
                        >
                          {currencyFormat({ number: price, bool: false })}
                        </Text>
                        <Text
                          color='black'
                          customClassName={styles.textPricePromotionCcuR}
                          fontSize='xs'
                          fontWeight='semibold'
                          truncate={30}
                          type='span'
                        >
                          c/u
                        </Text>
                        <Spacer.Vertical size={regularPriceSpacer} />
                      </Row>
                    </Column>
                  </Row>
                </>
              }

              {!isMobile && (<Spacer.Horizontal size={8} />)}

              <Row
                customClassName={styles.buyButtonAdd}
                justifyContent='center'
                padding='12px 0'
              >
                <AddToCartAndQuantity
                  buttonLabel={textButton}
                  customJustifyContent={buttonJustifyContent}
                  handleOnClick={handleOnClick}
                  isLoading={isLoading}
                  itemId={itemId}
                  quantity={quantity}
                  quantityButtonProps={quantityButtonProps}
                  quantityButtonStyles={quantityButtonStyles}
                  site='alvi'
                  statusButton={customButtonStatus}
                  tooltipProps={tooltipProps}
                />
              </Row>
            </Column>
          </Column>
        </Row>
      </Container>
    </Column>)
})

const MemoShelfAlvi = React.memo(ShelfAlvi)

export {
  ShelfAlvi,
  MemoShelfAlvi
}
